@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
.products {
  --count-products: 1;
  grid-template-columns: repeat(var(--count-products), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .products {
    --count-products: 2;
  }
}

@media (width >= 768px) {
  .products {
    --count-products: 3;
  }
}

@media (width >= 1024px) {
  .products {
    --count-products: 4;
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__photo {
  width: 160px;
  height: 134px;
  display: block;
}

.card__name {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  padding-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  text-align: right;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  padding-top: 24px;
  display: flex;
}

.card__price .price__title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price .price__num {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition-duration: .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

.card__stars {
  display: flex;
}

.card__stars .star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars--1 .star:nth-child(-n+1), .card__stars--2 .star:nth-child(-n+2), .card__stars--3 .star:nth-child(-n+3), .card__stars--4 .star:nth-child(-n+4), .card__stars--5 .star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  display: flex;
}

.navigation {
  align-items: center;
  display: flex;
}

.navigation__item {
  color: #060b35;
  text-transform: uppercase;
  margin-right: 20px;
  line-height: 60px;
  transition: color .3s;
  display: inline-block;
}

.navigation__item:hover {
  color: #00acdc;
  cursor: pointer;
}

.navigation__link .is-active {
  color: #00acdc;
  height: 60px;
  position: relative;
}

.navigation .navigation__link:last-child .navigation__item {
  margin-right: 0;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

body {
  margin: 0;
}

.navigation {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigation__item, .card__button {
  text-decoration: none;
}

.products, .card__photo {
  margin: 0 auto;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 500;
}

* {
  box-sizing: border-box;
}

