.card__stars {
  display: flex;

  .star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  @for $i from 1 through 5 {
    &--#{$i} .star:nth-child(-n + #{$i}) {
      background-image: url(../images/star-active.svg);
    }
  }
}
